"use strict";

import * as React from "react";
import { connect } from "react-redux";
import { Router, Route, Redirect, Switch } from "react-router-dom";
import { History } from "history";
import { createBrowserHistory } from "history";
import { Row, Col, FieldGroup, FormControl, Button } from "react-bootstrap";

import {  } from "./";
import { readCurrentUser } from "../redux/actions";
import { ReduxState } from "../redux/reducers";
import { CurrentUser } from "../models";

interface Props {
  currentUser: CurrentUser;
  loading: boolean;
  readCurrentUser(): void;
  login(): void;
}

interface State {
  username: string;
  password: string;
}

class Login extends React.Component <Props, State> {

  constructor(props: Props) {
    super(props);
    this.state = { username: "", password: "" };
  }

  componentDidMount() {
    const { currentUser } = this.props;
    if(!currentUser) {
      this.props.readCurrentUser();
    }
  }

  onChange = (field: "username" | "password", event: React.ChangeEvent<HTMLTextAreaElement>) => {
    const text = event.target.value;
    const state: any = { [field]: text };
    this.setState(state);
  }


  render() {
    const { currentUser } = this.props;
    if(currentUser) {
      return (<div>got current user </div>)
      // return (<Redirect to="/app/documents" />);
    } else {
      return (
        <Col xs={ 6 } xsOffset={ 3 }>
          { this.renderMain() }
        </Col>
      )
    }
  }

  renderMain() {
    const { loading, currentUser } = this.props;
    if(loading) {
      return "Loading...";
    }
    const { username, password } = this.state;
    return (
      <form>
        <FieldGroup
          controlId="username" validationState={ username == "" ? "error" : "success" } >
          <FormControl
            componentClass="textarea"
            value={ username }
            placeholder="Enter username"
            onChange={ this.onChange.bind("username") }
          />
          <FormControl.Feedback />
        </FieldGroup>
        <FieldGroup
          controlId="password" validationState={ password == "" ? "error" : "success" } >
          <FormControl
            componentClass="textarea"
            value={ password }
            placeholder="Enter password"
            onChange={ this.onChange.bind("password") }
          />
          <FormControl.Feedback />
        </FieldGroup>
        <Button onTouchTap={ this.props.login }>
          Login
        </Button>
      </form>
    );
  }

}

function mapStateToProps({ currentUser }: ReduxState) {
  const { loading, profile } = currentUser;
  return { loading, currentUser: profile };
}

export default connect(mapStateToProps, { readCurrentUser })(Login);



