"use strict";

import { Dispatch } from "redux";

import {
  READ,
  SAVE,
  START,
  FAIL,
  SUCCESS,
  CURRENT_USER,
  LOGIN_REQUIRED,
  LOG_IN,
  join
} from "../constants";
import { CurrentUser } from "../../models";

console.log(CurrentUser);

export function readCurrentUser() {
  return { type: join(READ, CURRENT_USER), payload: CurrentUser.get() };
};



